import Constant from './constant.js';

export function copyUrl(url){
	uni.showModal({
		content:"VR",
		confirmText:"",
		confirmColor:Constant.defaultThemeColor,
		success: (res) => {
			if(res.confirm){
				wx.setClipboardData({
					data: url,
					success:(data) =>{
						uni.showToast({
							icon:"none",
							title:""	
						})
					}
				})
			}
		}
	})
}